
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapGetters } from 'vuex'

export default {
components: {
Flow,
FlowState,
FlowLine,
Group,
Radio,
Cell,
CellBox,
Checklist,
CheckIcon,
XButton
},
data () {
return {
payType: [{
icon: require('./icons/支付宝Logo.jpg'),
key: '001',
value: '支付宝支付'
}, {
icon: require('./icons/微信Logo.jpg'),
key: '002',
value: '微信支付'
}],
payValue: '001',
payModeOneList: [{
key: 'payModeOne1',
value: '线上支付服务费，当面支付房租和押金给房东'
}, {
key: 'payModeOne2',
value: '线上支付服务费、房租和押金'
}],
payModeOneValue: 'payModeOne1',
payModeOne: true,
payModeTwo: false,
huaBei: true,
baiTiao: false,
orderPrice: 0
}
},
methods: {
change (val, label) {
console.log('change', val, label)
},
togglePayModeOne (payModeOne) {
if (payModeOne === true) {
this.payModeTwo = false
this.payModeOneValue = 'payModeOne1'
this.huaBei = false
} else {
this.payModeTwo = true
this.huaBei = true
this.payModeOneValue = ''
}
},
togglePayModeTwo (payModeTwo) {
if (payModeTwo === true) {
this.payModeOne = false
this.huaBei = true
this.payModeOneValue = ''
} else {
this.payModeOne = true
this.payModeOneValue = 'payModeOne1'
this.huaBei = false
}
}
},
computed: {
...mapGetters([
'currentHouseId',
'houseDetails'
]),
rentPrice () {
let total = 0
total = this.houseDetails[this.currentHouseId].abstract.price * 3
return total
},
depositPrice () {
let total = 0
total = this.houseDetails[this.currentHouseId].abstract.price
return total
},
serviceCharge () {
let total = 0
total = this.houseDetails[this.currentHouseId].abstract.price * 0.2
return total
},
totalPrice () {
let total = 0
if (this.payModeOne) {
if (this.payModeOneValue === 'payModeOne1') {
total = this.houseDetails[this.currentHouseId].abstract.price * 4.2
} else {
total = this.houseDetails[this.currentHouseId].abstract.price * 0.2
}
} else {
total = this.houseDetails[this.currentHouseId].abstract.price * 4.2
}
return total
}
},
created () {
this.payModeTwo = !this.payModeOne
}
}



