
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
components: {
Radio,
Group,
CheckIcon,
XButton,
Toast
},
data () {
return {
payType: [{
icon: require('./icons/微信Logo.jpg'),
key: '001',
value: '微信支付'
}, {
icon: require('./icons/支付宝Logo.jpg'),
key: '002',
value: '支付宝支付'
}],
payValue: '001',
agreeFlag: false,
position: 'middle',
showToastValue: false
}
},
methods: {
change (value, label) {
console.log('change:', value, label)
},
showToast (position) {
this.position = position
this.showToastValue = true
}
}
}



