import { mapGetters, mapActions } from 'vuex'

export const favoriteMixin = {
  computed: {
    ...mapGetters([
      'favoriteList'
    ])
  },
  methods: {
    toggleFavorite (house) {
      if (this.isFavorite(house)) {
        this.deleteFavoriteList(house)
      } else {
        this.saveFavoriteList(house)
      }
    },
    // 判断收藏列表中是否有该记录
    isFavorite (house) {
      const index = this.favoriteList.findIndex((item) => {
        return item.houseListId === house.houseListId
      })
      return index > -1
    },
    ...mapActions([
      'saveFavoriteList',
      'deleteFavoriteList'
    ])
  }
}




