import * as types from './mutation-types'
import {saveFavorite, deleteFavorite} from '../common/js/cache.js'

export const saveFavoriteList = function ({commit}, house) {
  commit(types.SET_FAVORITE_LIST, saveFavorite(house))
  commit(types.SET_ADD_LIKED, house.houseListId)
}

export const deleteFavoriteList = function ({commit}, house) {
  commit(types.SET_FAVORITE_LIST, deleteFavorite(house))
  commit(types.SET_MINUS_LIKED, house.houseListId)
}



