import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import BaiduMap from 'vue-baidu-map'
import VueLazyload from 'vue-lazyload'
import App from './App'
import store from './store'
// import Home from './components/HelloFromVux'
import RentHouse from './pages/RentHouse/RentHouse.vue'
import HouseDetails from './pages/HouseDetails/HouseDetails.vue'
import HouseMap from './pages/HouseMap/HouseMap.vue'
import HouseDetailMap from './pages/HouseDetailMap/HouseDetailMap.vue'
import Collection from './pages/Collection/Collection.vue'
import HouseList from './components/HouseList/HouseList.vue'
import LeaseHouse from './pages/LeaseHouse/LeaseHouse.vue'
import EntireRent from './pages/EntireRent/EntireRent.vue'
import JointRent from './pages/JointRent/JointRent.vue'
import AddressInput from './pages/AddressInput/AddressInput.vue'
import DetailsInput from './pages/DetailsInput/DetailsInput.vue'
import InformationCosts from './pages/InformationCosts/InformationCosts.vue'
import ContactType from './pages/ContactType/ContactType.vue'
import SignSubmit from './pages/SignSubmit/SignSubmit.vue'
import SignPay from './pages/SignPay/SignPay.vue'
import SignConfirm from './pages/SignConfirm/SignConfirm.vue'
import SignSuccess from './pages/SignSuccess/SignSuccess.vue'
import Mine from './pages/Mine/Mine.vue'

import './common/stylus/index.styl'

Vue.use(VueRouter)

Vue.use(BaiduMap, {
  // ak 是在百度地图开发者平台申请的密钥 详见 http://lbsyun.baidu.com/apiconsole/key */
  ak: '4AOGTQyDKKuHOTI3gKRmZfYo2Ifs7iov'
})

Vue.use(VueLazyload, {
  loading: require('./common/image/default.jpg')
})

const routes = [{
  path: '/',
  component: RentHouse
},
{
  path: '/RentHouse',
  component: RentHouse
},
{
  path: '/HouseDetails/:id',
  component: HouseDetails,
  children: [
    {
      path: '/HouseDetails/HouseDetailMap',
      component: HouseDetailMap
    }
  ]
},
{
  path: '/HouseMap',
  component: HouseMap
}, {
  path: '/Collection',
  component: Collection
}, {
  path: '/HouseList',
  component: HouseList
}, {
  path: '/LeaseHouse',
  component: LeaseHouse
}, {
  path: '/EntireRent',
  component: EntireRent
}, {
  path: '/JointRent',
  component: JointRent
}, {
  path: '/AddressInput',
  component: AddressInput
}, {
  path: '/DetailsInput',
  component: DetailsInput
}, {
  path: '/InformationCosts',
  component: InformationCosts
}, {
  path: '/ContactType',
  component: ContactType
}, {
  path: '/SignSubmit',
  component: SignSubmit
}, {
  path: '/SignPay',
  component: SignPay
}, {
  path: '/SignConfirm',
  component: SignConfirm
}, {
  path: '/SignSuccess',
  component: SignSuccess
}, {
  path: '/Mine',
  component: Mine
}]

const router = new VueRouter({
  routes,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})

FastClick.attach(document.body)

Vue.config.productionTip = false

// 路由跳转
Vue.prototype.$goRoute = function (index) {
  this.$router.push(index)
}

Vue.prototype.$goBack = function () {
  // this.$router.go(-1)
  this.$router.back()
}

/* eslint-disable no-new */
new Vue({
  router,
  render: h => h(App),
  store
}).$mount('#app-box')



