
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
components: {
Group,
XInput,
XAddress,
XButton
},
data () {
return {
geolocationHeight: document.body.clientWidth * 0.133335,  /* 20px */
addressData: ChinaAddressV4Data,
areaInputValue: [],
communityInputValue: '',
title: '默认为空',
center: {lng: 116.404, lat: 39.915}
}
},
methods: {
handler ({BMap, map}) {
this.center.lng = 116.404999
this.center.lat = 39.915
this.zoom = 15

var myGeo = new BMap.Geocoder()

var myAddress = this.communityInputValue
if (myAddress.length > 0) {
myGeo.getPoint(myAddress, function (point) {
if (point) {
map.centerAndZoom(point, 16)
map.addOverlay(new BMap.Marker(point))
console.log(point)
} else {
console.log('您选择的地址没有解析到结果!')
}
},
'北京市')
}
},
change (val) {
console.log('on change', val)
}
}
}



