
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapMutations } from 'vuex'
import { saveCurrentHouseId } from '../../common/js/cache.js'
import { favoriteMixin } from '../../common/js/mixin.js'

export default {
mixins: [favoriteMixin],
components: {
Group,
Cell,
Toast
},
data () {
return {
position: 'middle',
successfulCollectionValue: false,
cancelCollectionValue: false
}
},
props: {
houseList: {
type: Array,
default: []
}
},
methods: {
successfulCollection (position) {
this.position = position
this.successfulCollectionValue = true
},
cancelCollection (position) {
this.position = position
this.cancelCollectionValue = true
},
goDetails (index) {
this.$router.push('HouseDetails/' + index)
this.setCurrentHouseId(index)
saveCurrentHouseId(index)
},
...mapMutations([
'setCurrentHouseId',
'toggleCollect'
])
}
}



